            position: fixed;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, 0.75);
            z-index: 9999;

            .arrow {
                width: 88px;
                height: 125px;
                position: absolute;
                top: 8px;
                right: 30px;
                img {
                    width: 100%;
                    height: 100%;
                }
            }
            .arrow-text {
                position: absolute;
                top: 150px;
                left: 50%;
                width: 100%;
                transform: translate(-50%, 0);
                font-size: 16px;
                text-align: center;
                color: #fff;
            }

            .img {
                width: 50px;
                height: 50px;
                float: right;
            }
            .info {
                width: 100%;
                text-align: center;
                padding-top: 2rem;
                clear: both;
                .info-text {
                    font-size: 18px;
