<template>
    <div class="comp-footer-download"
         ref="downloadBox"
         v-clipboard="invitecode"
         v-if="showBanner === true">
        <!--<div class="close" @click="close">-->
            <!--<i class="iconfont icon-close"></i>-->
        <!--</div>-->
        <div class="left">
            <img src="../image/logo.png" alt="" height="48">
            <div class="text">
                <h5 class="title">{{messages[language].tweebaa}}</h5>
                <p>{{messages[language].downloadTips}}</p>
            </div>
        </div>
        <launch-app :type="type" :params="params">
            <div class="download-link">
                <a href="javascript:;" v-if="type === 'invite'">{{messages[language].download}}</a>
                <a href="javascript:;" v-else>{{messages[language].buttonText}}</a>
            </div>
        </launch-app>
    </div>
</template>
<script>
    import LaunchApp from '../components/LaunchApp.vue';
    export default {
        name: 'FooterDownload',
        props: {
            type: {
                type: String,
                default: ''
            },
            invitecode: {
                type: String,
                default: ''
            },
            params: {
                type: String,
                default: ''
            }
        },
        components: {
            LaunchApp
        },
        data() {
            return {
                showBanner: true,
                messages: {
                    en: {
                        tweebaa: 'Tweebaa',
                        downloadTips: 'Register now and earn a complimentary reward',
                        buttonText: 'Open In App',
                        download: 'Download'
                    },
                    zh: {
                        tweebaa: '推易吧',
                        downloadTips: '现在注册赚取更多奖励',
                        buttonText: '在App内打开',
                        download: '下载'
                    }
                }
            }
        },
        watch: {

        },
        created() {
            let ua = navigator.userAgent;
            if (ua.indexOf('baidu') > 0) {
                this.showBanner = false;
                window.setTimeout(() => {
                    this.showBanner = true;
                }, 1500);
            }
        },
        methods: {
            close() {
                this.showBanner = false;
            }
        }
    }
</script>
