
import axios from 'axios';

var navLanguage =  navigator.language.toLowerCase();
var language;

// var languages = ['zh','zh-cn','cn'];
// language = languages.includes(navLanguage) ? 'zh' : 'en';

if (navLanguage.indexOf('zh') > -1 || navLanguage.indexOf('cn') > -1) {
    language = 'zh';
} else {
    language = 'en';
}

function getAxois(url,method,perms) {
    return  axios({
        method:method,
        url:url,
        data:perms,

    })
}

function getNodeAxois(url,method,perms) {
    return  axios({
        method:method,
        url:url,
        data:perms,
    })
}

